$templateCache.put("app/routes/code/code.html","<div class=\"blue-bg\"><div class=\"blue-jumbo extra-padding-top\"><h2>Learn AngularJS!</h2>It\'s a great time to learn AngularJS! There are tons of great tips, tutorials, and examples out there on the internet. This page is a collection of many useful links about AngularJS.<hr><div class=\"code-section\"><h2>Javacsript, HTML, and CSS</h2></div><div class=\"code-section\"><h2>Angular Basics</h2>Beginner Tutorials: Intro Videos:</div><div class=\"code-section\"><h2>Best Practices</h2><h3>Style Guides</h3><p>The style guides are meant to give you a deeper set of rules for how to structure your code that go beyond basic syntax.</p><a href=\"https://github.com/johnpapa/angular-styleguide\">John Papa\'s</a> <a href=\"https://github.com/toddmotto/angular-styleguide\">Todd Motto\'s</a> <a href=\"https://github.com/mgechev/angularjs-style-guide\">Minko Getchev\'s</a> <a href=\"http://www.codestyle.co/Guidelines/angularjs\">CodeStyle\'s</a></div>Angular Classes<div class=\"code-section\">Angular Books</div><div class=\"code-section\">Angular Tutorials</div><h1>Angular Concepts</h1>Here are some tips and links to more specific concepts in Angularjs.<h2>Services</h2>Services allow you to break up Javascript code into modular segments.<h3>Service Service</h3><h3>Factory Service</h3><h3>Value Service</h3><h3>Constant Service</h3><h2></h2><h2>Workflow</h2>Gulp, Bower, and Yeoman are awesome tools for increasing your productivity. Official NG-NJ Yeoman Generator: Gulp-Angular<h2>Custom Directives</h2>Custom Directives allow you to put new commands on your HTML tags so you can break up your HTML into reusable components.<h2>Backend</h2>The official server side scripting language for NG-NJ is NodeJS. Firebase - Great for prototyping and making fully functional applications <i>fast.</i> MEAN Stack - This is a common backend setup that uses NodeJS and MongoDB. Loopback / Strongloop - allows you to write one query language and easily swap out different databases.<h2>UI Frameworks</h2>Angular Material - Official Angular ui from Google. Follows Google\'s Material Design specifications. Also includes a flexbox layout structure. Bootstrap - Classic framework for laying out a page on different screen sizes and includes some cool icons.<h2>Unit Testing</h2>Our suggested generator Gulp-Angular uses karma and jasmine for unit tests (gulp test) and protractor (gulp test:e2e).<h2>Fiddles</h2><h1>Filters></h1>On this page you can find AngularJS code and examples from past events.<div>If you would like to suggest a link to be added to this page, <a>please submit a request here</a>.</div></div></div>");
