$templateCache.put("app/routes/main/main.html","<div layout=\"vertical\" layout-fill=\"\"><md-content layout-fill=\"\" class=\"video-container\"><div class=\"md-whiteframe-z2 blue-jumbo\"><h1>GL Welcome to_ NG-NJ!!</h1><p class=\"lead\"><img src=\"assets/images/ng-nj-logo.png\" width=\"150px\" alt=\"ng-nj logo\"><br>A fun and friendly group for Angular Developers in New Jersey.</p></div><div layout=\"row\" class=\"card-container\"><div class=\"card\"><h2 ui-sref=\"events\" class=\"card-header\">Exciting Upcoming Events</h2><div class=\"card-text\">This is a list of upcoming events in and around New Jersey, USA eg. meetups, conferences, hackathons, and more.</div></div><div class=\"card\"><h2 ui-sref=\"about-us({userId:\'cheese\'})\" class=\"card-header\">Tight Underground Community</h2><div class=\"card-text\">Join a group experienced professional programmers who share code, ideas, and advice to each other.</div></div><div class=\"card\"><h2 ui-sref=\"register\" class=\"card-header\">Free Membership Available</h2><div class=\"card-text\">There\'s no cost to join NG-NJ and gain access to all of our member benefits. You don\'t even have to be from NJ!</div></div></div></md-content></div>");
