$templateCache.put("app/routes/privacy/privacy.html","<div class=\"full-privacy-policy-page\"><h1>Privacy Policy</h1>NG-NJ Privacy Policy Welcome to NG-NJ®, a service provided by NG-NJ Limited (“NG-NJ”, “we” “our”, “us”). Your privacy is important to us. This Privacy Policy explains how we collect, store, use and disclose your information when you use NG-NJ.com and m.NG-NJ.com (together, the “Website”), our mobile and desktop apps (the \"Apps\") and all related sites, players, widgets, tools, apps, data, software, APIs and other services provided by NG-NJ (the “Services”). This Privacy Policy explains the following, amongst other things: Our principles with respect to your information and your privacy The information we collect about you How we use your information How we share your information How we use cookies and similar technology Your choices with respect to your information How to contact us regarding privacy issues By using the Website, the Apps or any of the Services, and in particular by registering a NG-NJ® account, you are consenting to the use of your information in the manner set out in this Privacy Policy. For your convenience, information relating to our use of cookies and similar technologies is set out in a separate Cookies Policy. The Cookies Policy forms part of the Privacy Policy, and whenever we refer to the Privacy Policy, we are referring to the Privacy Policy incorporating the Cookies Policy. Please take some time to read this Privacy Policy (including the Cookies Policy), and make sure you are happy with our use and disclosure of your information. If you do not agree to any of the provisions of this Privacy Policy, you should not use the Website, the Apps or any of the Services. If you have any questions or concerns, you can contact us at privacy@NG-NJ.com. Please note that this Privacy Policy only applies to the Website, the Apps and the Services (together, the “Platform”). When using the Platform, you may find links to other websites, apps and services, or tools that enable you to share information with other websites, apps and services. NG-NJ is not responsible for the privacy practices of these other websites, apps and services and we recommend that you review the privacy policies of each of these websites, apps or services before connecting your NG-NJ® account or sharing any personal data. Our Principles We have a pretty simple approach to data protection and privacy, which can be summarized as follows: You should know exactly what we do with your information This Privacy Policy is designed to give you full transparency regarding our data protection practices. If there’s anything that’s not clear from this Privacy Policy, please feel free to email us at privacy@NG-NJ.com. You should have full control over your information We’ve designed the Website to give you control over the information you publish and share using NG-NJ® and other sites and services to which you connect your NG-NJ® account. Please take full advantage of these tools and make sure you only share what you want to share. Information we collect about you We collect information about you from various sources: Information provided by you You don’t need to provide us with any personal data in order to visit the Website. However, certain Services do require that you register for a NG-NJ® account and, by doing so, you will provide us with certain information: Essential information: When you register any kind of NG-NJ® account, you will need to provide your email address (except when you sign-up with Facebook and don’t give your permission to share it with NG-NJ), your month and year of birth, and choose a password. In addition, if you purchase a Subscription, you will also need to provide your real name, billing address and payment verification information. Non-essential information: Your gender if you wish to provide it. Profile information: You may choose, at your discretion, to provide additional information for your public profile on NG-NJ® – for example: your real name a user name (which may be your real name or a pseudonym) the city and country in which you live a profile picture, profile header image or avatar information about how you categorize yourself with respect to your content (for example, whether you are a musician, a label or some other category of creator) details of your other websites and social media profiles, including links to those websites and profiles None of this profile information is mandatory, and any information you do provide may be deleted, edited, changed or amended by you at any time. For more information, see the Choice and Control section below. Information from correspondence: You will provide certain personal data if you contact us by email, use any of the webforms on the Website, or contact us by mail, fax or other offline means. Survey information: If you participate in any survey, you will provide certain personal data as part of your response, unless you respond anonymously. Information that you post: You may provide personal data when you upload sounds to the Platform, or when you post comments, or contribute to community discussions. Information we collect automatically There is certain information that we collect automatically as the result of your use of the Platform, or through the use of web analytics services as described in our Cookies Policy. This information includes but is not limited to: the Internet Protocol (IP) address of the device from which you access the Platform (this can sometimes be used to derive the country or city from which you are accessing the Platform) the site that you visited immediately prior to visiting the Website the specific actions that you take on the Platform, including the pages that you visit, streaming, offline listening or downloading tracks, uploading or recording tracks, connecting your Facebook or Google+ account, sharing a sound with another user, following or unfollowing another user, joining or leaving a group, posting a comment, or performing a search any search terms that you may enter on the Website the time, frequency and duration of your visits to the Platform your browser type and operating system the nature of the device from which you are accessing the Platform, for example, whether you are accessing the Platform from a personal computer or from a mobile device and its device-ID your location data (e.g. GPS) when using NG-NJ Apps, information collected through cookies and similar technology, as described in our Cookies Policy information regarding your interaction with email messages, for example, whether you opened, clicked on, or forwarded the email message Information from other sources Facebook Connect NG-NJ allows you to sign up and log in using your Facebook account. If you sign up using Facebook, Facebook will ask your permission to share certain information from your Facebook account with NG-NJ. This includes your first name, last name, email address in order for us to verify your identity and gender, general location, a link to your Facebook profile, your timezone, birthday, profile picture, your \"likes\" and your list of friends. This information is collected by Facebook and is provided to us under the terms of Facebook’s privacy policy. You can control the information that we receive from Facebook using the privacy settings in your Facebook account. If you sign up to NG-NJ® using Facebook, your NG-NJ® account will be connected automatically to your Facebook account, and information regarding your activity on NG-NJ®, specifically any new public sounds that you post on NG-NJ® and any new favourites, will be shared with Facebook and will be published in your timeline and your Facebook friends’ newsfeeds. If you do not wish to share your NG-NJ® activity with your Facebook friends, you can control this from the Settings page on the Website – for more information, see the Choice and Control section below. If you signed up using Facebook by mistake, you can delete your account from the Account page within your Settings at any time, or you can email us at privacy@NG-NJ.com. Google+ You can sign up and log in to NG-NJ using your Google+ account. When you sign up using Google+, Google will ask your permission to share certain information from your Google+ account with NG-NJ. This includes your first name, last name, email address in order for us to verify your identity and gender, a link to your Google+ profile, your profile picture, and your list of friends. This information is collected by Google and is provided to us under the terms of Google\'s privacy policy. If you sign up to NG-NJ® using Google+, by default information regarding your activity on NG-NJ® will be visible on Google to everyone in your Google+ circles and shared with Google according to Google\'s terms of service and Google\'s privacy policy. For more information on how to manage the activities you share back to your Google+ account, see Google\'s support page. You can manage who from your Google+ circles sees your NG-NJ® activity from within your Google+ App settings page. If you want to control the type of NG-NJ® activity that is shared with your Google+ circles, you can do this from the settings page of your NG-NJ account under the “Connections” tab. If you signed up to NG-NJ® using Google+ by mistake, you can delete your account from the account page within your settings at any time, or you can email us at privacy@NG-NJ.com. How we use your information We use the information that we collect about you for the following purposes: Use of and interaction with the Platform: To operate and maintain your NG-NJ® account, and to provide you with access to the Website and use of the Apps and any Services that you may request from time to time. Your email address and password are used to identify you when you sign into the Platform. Your device-IDs are used to ensure that you are in control of the devices that have access to your Subscription. To identify you as the creator of the sounds that you upload, the comments that you post and/or the other contributions that you make to the Platform. To seek your participation in surveys, and to conduct and analyze the results of those surveys if you choose to participate. To provide you with technical support. To respond to you about any comment or enquiry you have submitted. To provide other users with data regarding people\'s listening behavior of their sounds, i.e. streaming and downloading their sounds or playlists or following their profiles. To prevent or take action against activities that are, or may be, in breach of our Terms of Use, Community Guidelines or applicable law. We use your month and year of birth to enable age restriction. Any additional information that you provide as part of your public profile, such as your real name, and links to your website and other social media profiles (but not your email address), will be published on your profile page. This information will be publicly accessible and may be viewed by anyone accessing the Website, or using our API or other Services. Please bear this in mind when considering whether to provide any additional information about yourself. If you purchase a Subscription, your name, address and payment verification information will be used to process your account subscription and renewals and to collect your payment. All payment verification information is transmitted using industry-standard SSL (Secure Socket Layer) encryption. NG-NJ also complies with the Payment Card Industry Security Standards. Your email address will be used to send you service updates and notifications regarding your account, and (if you have elected to receive them using your account preferences) newsletters, marketing messages and certain email notifications. For information about how to change your preferences, and to unsubscribe from newsletters, marketing messages and email notifications, please see the Choice and Control section, below. If you are a Facebook or Google+ user, and one of your Facebook or Google+ friends registers for a NG-NJ® account, we may let your friends who already are on NG-NJ know that you joined or will suggest to your Facebook or Google+ friend that you might be someone they may wish to follow on NG-NJ. If you are a Facebook or Google+ user and sign up using Facebook or Google+ , we may also use the information we receive about you from Facebook or Google to suggest or connect you to users that you may wish to follow on NG-NJ®. It will be up to you if you choose to follow these users and you can unfollow them at any time. Improving and customizing the Platform and advertising: To analyze the use of the Platform, and the people visiting the Website and using the Apps and Services, in order to improve NG-NJ® and customize it to your needs and interests. To show you interesting NG-NJ ads, when you visit third party websites. When you see our ads on a third party website or apps, we or third party services we engage for this purpose, may collect information your browser or mobile app makes available when seeing the ad (e.g. ip-addresses, referral URL, browser details, timestamp of request). Third party websites or apps may also allow us to measure the success of our ads on their websites. This may include e.g. whether a click converted to an actual purchase of a Subscription. Please see the privacy policy of such third party websites to understand what data may be collected and how to opt-out. To show you ads on behalf of advertisers, we use information that you make available to us when you interact with the Platform (e.g. by listening, liking or creating playlists), including location data, together with information you provide to us through the Platform, e.g. demographic information, you choose to make available to us (such as your age or gender) directly or through third party services as well as information your browser sends us when using the Platform (e.g. referral URLs, device type or location data). This allows us to show you ads that are more relevant and interesting to you. Please note that we do not share personal data with advertisers. Instead, advertisers pre-define a specific audience by certain generic criteria (e.g. the city of residence, the age range, or the number of followers of an unidentified user) for a specific ad campaign. If your profile matches these generic criteria, we may choose to display a suitable ad to you. We may also display ads based on the content that you have recently viewed or searched. Before we may use location data from the app for the first time, your mobile device will generally ask you for permission first. In case you gave permission to do so, location based services in your device will collect location data for a couple of seconds. These will then be shared with us or a respective service provider. When we engage third party services such as ad-networks that enable us showing you interest based ads, we may also allow them to collect certain information such as cookie data from you. This information would allow these third-parties to deliver targeted advertisements that they believe will be of most interest to you. If you allow third party websites to collect and share data when you interact with their platform, ad-networks may use that information together with the information we allow them to collect on the Platform. Please see the privacy policy of such third party websites to understand what data may be collected and how to opt-out. To customize your use of the Platform and/or the content of any email newsletter or other material that we may send to you from time to time. For other purposes, provided we disclose this to you at the relevant time, and provided that you agree to the proposed use of your personal data. Sharing of your information We will not share your information with any third party, except as described in this Privacy Policy (including our Cookies Policy). There are circumstances where we may need to share some of the information we collect about you or which you provide to us - these circumstances are as follows: Other users: Any information in your public profile (other than your email address) will be accessible by other users of the Platform, who may view your profile information, listen to and comment on any of your public sounds, add themselves to your list of followers, and send you messages. If you listen to any sounds uploaded by a pro user, the fact that you have listened to those sounds will be shared with that pro user as well as that you are following that pro user now, so that they can track the popularity of their sounds. Similarly, if you comment on any sounds on the Platform, your comments will be available to all users of the Platform. With your consent: We will disclose your information if you have explicitly agreed that we may do so. We will make this clear to you at the point at which we collect your information. Service providers: We use certain reputable third parties, some of whom may be located outside of the European Economic Area, to provide us with certain specialized services related to the Platform. These third parties will have access to certain information about you, but only where this is necessary in order for those third parties to provide their services to us. Where we transfer personal data to these third parties, we ask and require these third parties to implement appropriate organizational and technical security measures to protect against unauthorized disclosure of personal data, and only to process personal data in accordance with our instructions and to the extent necessary to provide their services to us. Users of our API: NG-NJ has an open API, which enables application developers to integrate elements of the Platform into exciting new apps. Some of these developers may be located outside of the European Economic Area. Any information that you choose to make public on the Platform, including your public profile information and any public sounds, may be accessible to these developers. Please bear this in mind when deciding what information to publish on your profile. For information on how to limit the information that is available over the API, please see the Choice and Control section below. Other networks and connected apps: If you choose to connect your NG-NJ® account to other apps or social networks such as Facebook, Google+, Tumblr or Twitter, you will be sharing certain information with these apps or networks. In the case of Facebook, Google+, Tumblr or Twitter, any new public sounds that you post or listen to on NG-NJ® or any new favourites will be shared to those networks and will be visible to your followers and friends on those networks. In the case of connected apps, you will need to provide the app developer with full access to your NG-NJ® account in order to upload and download sounds, or comment on, favourite or repost sounds from within the app. This will mean sharing your public information, plus access to your private sounds. NG-NJ is not responsible for these other apps or networks, so please make sure you only connect your account with apps or services that you trust. For information on how to control the information that your share, and how to disconnect your account from other networks or apps, please see the Choice and Control section below. Advertisers may use third party services to ensure their ads are properly served and to measure the success of their campaigns on websites and apps. We allow these services to collect and use the information for such purposes. As aggregated data: We may aggregate your personal data with similar data relating to other users of the Platform in order to create statistical information regarding the Platform and its use, which we may then share with third parties or make publicly available. However, none of this information would include any email address or other contact information, or anything that could be used to identify you individually, either online or in real life. If required by law: We will disclose your information if we believe in good faith that we are permitted or required to do so by law, including in response to a court order, subpoena or other legal demand or request. To protect our interests: We may disclose your information if we feel this is necessary in order to protect or defend our legitimate rights and interests, or those of our users, employees, directors or shareholders, and/or to ensure the safety and security of the Platform and/or the NG-NJ® community. In the context of a business transfer: We may transfer your information to any person or company that acquires all or substantially all of the assets or business of NG-NJ, or on a merger of our business, or in the event of our insolvency. Cookies and similar technology In common with most websites, we use cookies and other standard Internet technologies to help us improve the Website and the Apps and Services we provide. We have included information about our use of cookies and similar technology in a separate Cookies Policy that forms part of this Privacy Policy. The Cookies Policy also includes information about how you can block or disable third party cookies (which we only use for the purposes of analyzing the use of our Platform and optimizing the content on our Platform), and how to opt out of other technologies such as bug reporting within our mobile apps. Ads displayed to you are in some cases provided by third parties. These may and as far as allowed by your settings or your device, use information about your visit and interaction with the Platform in order to show you personalized ads of products and services that may be of interest to you. Personal information such as your name or email address will not be shared with these. We have done our best to provide you with as much information as possible about our use of cookies and similar technology. If you choose to use the Platform without blocking or disabling these cookies or opting out of other technologies as described in our Cookies Policy, you will indicate your consent to our use of these technologies and to our use (in accordance with this Privacy Policy and our Cookies Policy) of any personal data that we collect using these technologies. Choice and control We do our best to give you as much choice as possible regarding the amount of information you provide to us, and the control you have over that information. It is not necessary for you to provide us with any personal data in order to visit the Website, although certain information will be collected automatically by virtue of your visit (as described above). However, if you do decide to register a NG-NJ® account, you can control your information in the following ways. Email and notification preferences When you are signed into your account, you can access your Settings by clicking on the three-dots-tab at the top right of the screen, and selecting Settings from the drop down menu. From the Notifications tab you can tell us: which email notifications (if any) you would like to receive from us; and which Activity Notifications (if any) you would like to receive from us. You can also turn off email notifications from the Notifications tab within your Settings. This will immediately disable all email notifications, other than essential account notifications such as important service updates, reports of copyright infringement and other essential information relating to your account and your use of the Platform. It is also possible to unsubscribe from email newsletters and notifications (other than essential account notifications) at any time by clicking on the link at the end of each email. You can add additional or alternative email addresses that you may wish us to use to communicate with you in your Account Settings tab. Sharing with other apps and networks The Connections tab within your Settings on the Website enables you to control the information you share with other networks and applications. As well as selecting those of your social networks you wish to share to, you can also control access to other services and applications to which you have connected your NG-NJ® account. If you sign up for NG-NJ® using your Facebook or Google+ account, your NG-NJ® account and your Facebook or Google+ account respectively will be connected automatically. Information regarding the public sounds that you post, listen or favourite on NG-NJ®, will be shared with the connected Facebook or Google+ account. Content that you share with Facebook will appear on your timeline and in your Facebook friends’ newsfeeds. Content you share with Google will be seen by the people from your Google+ circles when they visit your Google+ profile, make relevant Google searches, on Google-served ads, and the services selected in your Google account settings. If you do not want this information shared with Facebook or Google, please change your Settings. Please note that this Privacy Policy does not apply to any third party sites or applications, and we cannot control the activities of those sites or applications. You are advised to read the privacy policies of those sites or applications before sharing your information with, or connecting your NG-NJ® account to, any of these third party sites or applications. Sharing with other users NG-NJ® is a very social place, but if you would prefer to browse the Website anonymously, you can do so by checking the Anonymous Listening box in the Account tab within your Settings on the Website. By checking this box, other users will not be able to see the sounds that you listened to, but we will still have access to this information. If any of your sounds contain any personal data or any other information which you would not like to share, you can control who has access to these sounds using the settings in the sound page (click on the pencil icon in the waveform player for the relevant sounds). By setting your sounds to “private”, rather than “public”, you can limit who has access to your sounds. Sharing with app developers NG-NJ® has an open API, which allows third party developers to build some really cool apps as an extension of the Platform. If you would prefer that your sounds are not made available to third party app developers, you can disable API access within the sound page for each of the sounds that you upload. Click on the pencil icon in the waveform player, and uncheck the “Apps enabled” box at the bottom of the page. Please note that your public profile information will still be accessible, but this does not include anything that is not publicly available on the Website. Customizing the Platform and personalized ads We may also use third party services using cookies or similar technologies showing you personalized ads on our behalf. Please see our Cookies Policy for how to opt-out of those. Please be aware that even if you opt out of interest-based ads, you may still see ads, which likely are not as appealing to you. Cookies Information on how to block or disable cookies is included in our Cookies Policy. Further information is available at http://aboutcookies.org. App preferences You can use the settings within your iOS device or within the Android app to opt-out of analytics tracking and bug reporting. For the opt-out, if you are using our iOS app, go to the settings of your iOS device and open the NG-NJ® tab. With an Android device you need to open the NG-NJ® app and open the basic settings tab within. From there you can simply disable analytics and/or bug reporting to stop any analytics data or bug reporting information being sent from the app. Further information is available in our Cookies Policy. Deleting your account You can delete your account from the Account page within your Settings on the Website at any time. Please bear in mind that, if you delete your account, all data associated with your account, including the sounds that you have uploaded and the usage data associated with those sounds, will be deleted and may not be recoverable. You are therefore advised to copy or back up all content uploaded to your account before you delete your account. Even if you delete your NG-NJ® account, it is possible that your information may still show up in some internet search results for a short while afterwards, if the search engine maintains a temporary cache of web pages. Search engines\' caching processes are outside of NG-NJ’s control and therefore we cannot be responsible for any information that remains cached by search engines after that information has been removed from the Platform. Please note that deleting any NG-NJ® Apps, or any third party apps to which your NG-NJ® account is connected, will not delete your NG-NJ® account. If you wish to delete your account, you will need to do so from the Account tab in your Settings on the Website. Similarly, if you delete a third party app that is connected to your NG-NJ® account over our API, this will not necessarily delete all data from within that third party app. You will need to refer to the app provider’s terms of use and privacy policy to find out how to delete your data from that third party app. Access to your data As described above, most of the personal data and other information you provide to us can be accessed and updated in the Settings page of your NG-NJ® account. If you wish to access, amend or delete any other personal data we hold about you, or if you have any objection to the processing of any personal data that we hold about you, please contact us at privacy@NG-NJ.com, or the address provided below. If you ask us to delete your account (either via the Settings page or by email), we will do so within a reasonable period of time, but we may need to retain some of your personal data in order to satisfy our legal obligations, or where we have a legitimate reason for doing so. International data transfers NG-NJ is based in Europe, and your personal information is collected, stored, used and shared in accordance with European laws. However, from time to time, it may be necessary for us to transfer your personal data to the United States. You should be aware that privacy laws in the United States may not be equivalent to the laws in your country, and by using the Platform, you consent to the transfer, storage and processing of your personal data in the United States in accordance with this Privacy Policy and applicable law. Children NG-NJ® is not intended for use by children. Anyone under the age of 13 is not permitted to use the Platform and must not attempt to register an account or submit any personal information to us. We do not knowingly collect any personal information from any person who is under the age of 13 or allow them to register an account. If it comes to our attention that we have collected personal data from a person under the age of 13, we will delete this information as quickly as possible. If you have reason to believe that we may have collected any such personal data, please notify us immediately at privacy@NG-NJ.com. Changes and updates to this Privacy Policy We may occasionally update this Privacy Policy, and when we do so, we will also revise the Effective Date set out below. Any changes to our Privacy Policy will always be available here so that NG-NJ® users are always aware of what information we gather, and how we might use and share that information. Please be sure to check back here from time to time to ensure that you are aware of any changes to this Privacy Policy. Any material changes to this Privacy Policy will be communicated to registered users by a notification to their account and/or by posting a notice of the change on the Website. Questions? If you have questions about this Privacy Policy, want to suggest changes to this Privacy Policy or want to know, what information we store about you, please contact us by email at privacy@NG-NJ.com, or write to us at: NG-NJ Limited Rheinsberger Str. 76/77 10115 Berlin Germany Attention: Legal Department Don’t want to give us your information? If you decide that you do not want us to use your information in the manner described in the Privacy Policy (including our Cookies Policy), please do not use the Platform. If you have already registered an account, you can find out how to terminate or cancel your account here. Last amended: 29 March 2016 Link to prior versions You will find a link to prior versions of our Privacy Policy and a summary of the change(s) below. January 2016 - Email-less sign-up and interest based advertising. June 2015 - Change in Analytics Services. August 2014 - Data available to OnNG-NJ users. July 2014 - Collecting gender information. March 2014 - Collecting month and year of birth. May 2013</div>");
